<template>
    <el-card>
        <el-row >
            <el-col :span="3"><div class="item current" >供应商管理</div></el-col>
            <el-col :span="3"><div class="item" @click="todo('/client')">客户管理</div></el-col>
            
        </el-row>
        <el-form ref="form" label-width="120px">
            <el-row >
                <el-col :span="6">  
                    <el-form-item label="供应商名称">
                        <el-input ></el-input>
                    </el-form-item>
                </el-col>
                
                <el-col :span="6" :offset="12">  
                    <el-button type="primary">筛选</el-button>
                    <el-button type="primary" @click="addSupplier">新增</el-button>
                    <el-button>清空</el-button>
                </el-col>
            </el-row>
            <el-table
            :header-cell-style="{background:'#f0f5ff'}"
            :data="tableData"
            style="width: 100%;margin-top:0">
                <el-table-column
                    prop="date"
                    label="日期"
                    >
                </el-table-column>
                <el-table-column
                    prop="name"
                    label="姓名"  
                    >
                </el-table-column>
                <el-table-column
                    prop="address"
                    label="地址">
                </el-table-column>
                <el-table-column
                    label="供应商状态">
                    <template>
                        <el-switch></el-switch>
                    </template>
                </el-table-column>
                <el-table-column                  
                    label="操作">
                    <template >                     
                            <el-button type="primary"  size="mini">编辑</el-button>                            
                            <el-button type="danger"  size="mini">删除</el-button>                     
                    </template>
                </el-table-column>             
            </el-table>   
        </el-form>
        <el-pagination
                    :current-page="4"
                    :page-sizes="[100, 200, 300, 400]"
                    :page-size="100"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="400">
        </el-pagination>
    </el-card>
</template>
<script>
export default {
    inject: ['reload'],
    data(){
        return{
            tableData: [{
                date: '2016-05-02',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1518 弄'
            }, {
                date: '2016-05-04',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1517 弄'
            }, {
                date: '2016-05-01',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1519 弄'
            }, {
                date: '2016-05-03',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1516 弄'
            }]
        }
    },
    methods: {
        addSupplier(){
            this.$router.push('/addsupplier')
        },
        // 跳转页面
        todo(path) {
        window.sessionStorage.setItem('activePath', path)
        this.$router.push(path)
        this.reload()
        }
    },
}
</script>
<style lang="less" scoped>
.el-card{
    width: 100%;
    height: 100%;
    position: relative;
    .item{
        font-weight: bold;
        font-size: 20px;
        text-align: center;
        margin-bottom: 30px;
        cursor: pointer;
    }
    .current {
      position: relative;
      color: #0790ff;
      &:after {
        content: ' ';
        width: 100px;
        height: 4px;
        background: #0790ff;
        position: absolute;
        bottom: -7px;
        border-radius: 2px;
        left: 50%;
        transform: translateX(-50%);
      }
    }
    .el-pagination{
      position: absolute;
      bottom: 30px;
      right: 30px;
  }
}
</style>


